<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body row p-0">
        <div class="col-sm-12">
          <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
        </div>
        <div class="col-sm-12">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="pages" :actions="actions" :actionsTable="actionsTable" @sortChange="sortChange"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { MessageBox, Message } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import pageSchemas from './page-schemas'
  import dtHelper from 'src/helpers/datatable'

  export default{
    components: {
      MyTable,
      MyFilter
    },
    computed: {
      pages() {
        return this.$store.state.pages;
      }
    },
    data () {
      return {
        actions: [],
        filterOutput: [],
        columnDefs: dtHelper.buildColumDefs(pageSchemas),
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          },
          {
            type: 'danger',
            icon: 'nc-icon nc-simple-remove',
            callback: this.remove
          },
        ],
        actionsTable: [
          {
            title: 'Ẩn',
            callback: this.inactiveAll
          },
          {
            title: 'Hiện',
            callback: this.activeAll
          },
          {
            title: 'Xóa',
            color: 'text-danger',
            callback: this.removeAll
          }
        ],
        filter: {},
        rules: dtHelper.buildRules(pageSchemas)
      }
    },
    mounted() {
      window.AP = this;
      this.$store.dispatch('fetchPages');

      this.$store.dispatch('setPageTitle', 'trang nội dung');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Tạo trang nội dung',
        type: 'primary',
        icon: '',
        callback: this.createPage
      }])
    },
    methods: {
      updateFilter(filterOutput) {
        let filterString = dtHelper.buildQueryString(filterOutput);
        this.$store.dispatch('fetchPages', {
          filterString: filterString
        });
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchPages', {
          order: prop + '=' + order
        });
      },
      edit(index, row){
        this.$router.push('/pages/' + row.id);
      },
      createPage(){
        this.$router.push('/pages/create');
      },
      remove(index, row){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.$store.dispatch('removePage', row.id).then(res => {
            Message({
              type: 'success',
              message: 'Đã xóa trang nội dung'
            });
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hủy bỏ'
          });
        })
      },
      inactiveAll(rows){
        this.updateStatus(rows, 'inactive');
      },
      activeAll(rows){
        this.updateStatus(rows, 'active');
      },
      removeAll(rows){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.updateStatus(rows, 'delete');
        })
      },
      updateStatus(rows, status){
        let self = this;
        this.$util.updateStatusAll('page', rows, status).then(function (result) {
          self.$store.dispatch('fetchPages');
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
